import React from 'react';
import moment from 'moment';
import './Message.scss';

export default function Message(props) {
  const {
    data,
    isMine,
    startsSequence,
    endsSequence,
    showTimestamp
  } = props;

  const friendlyTimestamp = moment(data.timestamp).format('h:mm a');
  return (
    <div className={[
      'message',
      `${isMine ? 'mine' : ''}`,
      `${startsSequence ? 'start' : ''}`,
      `${endsSequence ? 'end' : ''}`
    ].join(' ')}>

      <div className="bubble-container">
        <div className="bubble" title={friendlyTimestamp}>
          <img src={data.photo} alt={friendlyTimestamp} className={`m${!isMine ? 'r' : 'l'}-2 message-conversation-photo d-flex float-${isMine ? 'right' : 'left'} align-items-start`} />
          <p className={'message'} style={{ textAlign: 'justify' }}>
            {data.message}
          </p>
          <div className="timestamp d-flex align-items-end pt-1">
            {friendlyTimestamp}
          </div>
        </div>
      </div>
    </div>
  );
}